<template>
    <div>
        <mhead-top titleHead="发票" lfIcon="el-icon-arrow-left icon" lfUrl="/userCenter/invoice/invoice" rgIcon="el-icon-s-home icon" rgUrl="/home"></mhead-top>
        <div class="mBg">
            <div class="m-edit-add">
                <div class="m_sel flex f-nowrap">
                    <div class="m_tit">发票类型：</div>
                    <div class="m_choice">
                        {{invoiceType==0?'企业':'个人'}}
                    </div>
                </div>
                <div  v-if="invoiceType=='0'">
                    <el-form :model="companyForm" :rules="companyRules" ref="companyForm" label-position="top">
                        <el-form-item class="m-f-h" label="公司名称" :label-width="formLabelWidth" prop="ename">
                            <el-input size="medium" v-model="companyForm.ename" ></el-input>
                        </el-form-item>
                        <el-form-item class="m-f-h" label="公司税号" :label-width="formLabelWidth" prop="taxnumber">
                            <el-input size="medium" v-model="companyForm.taxnumber" ></el-input>
                        </el-form-item>
                        <el-form-item class="m-f-h" label="开票地址" :label-width="formLabelWidth" prop="address">
                            <el-input size="medium" type="textarea" v-model="companyForm.address"></el-input>
                        </el-form-item>
                        <el-form-item class="m-f-h" label="电话号码" :label-width="formLabelWidth" prop="phone">
                            <el-input size="medium" v-model="companyForm.phone" ></el-input>
                        </el-form-item>
                        <el-form-item class="m-f-h" label="开户银行" :label-width="formLabelWidth" prop="bank">
                            <el-input size="medium" v-model="companyForm.bank" ></el-input>
                        </el-form-item>
                        <el-form-item class="m-f-h" label="银行账户" :label-width="formLabelWidth" prop="banknumber">
                            <el-input size="medium" v-model="companyForm.banknumber" ></el-input>
                        </el-form-item>
                    </el-form>
                    <div class="formBtn">
                        <div class="m-edit-btn" @click="cancel">取消</div>
                        <div class="m-edit-btn safe" @click="submitForm('companyForm',companyForm.invoice_id)">保存</div>
                    </div>
                </div>
                <div v-else>
                    <el-form :model="personForm" :rules="personRules" ref="personForm"  label-width="90px" label-position="top">
                        <el-form-item class="m-f-h" label="名称" :label-width="formLabelWidth" prop="title">
                            <el-input size="medium" v-model="personForm.title" ></el-input>
                        </el-form-item>
                        <el-form-item class="m-f-h" label="地址" :label-width="formLabelWidth" prop="address">
                            <el-input size="medium" type="textarea" v-model="personForm.address" ></el-input>
                        </el-form-item>
                        <el-form-item class="m-f-h" label="电话号码" :label-width="formLabelWidth" prop="phone">
                            <el-input size="medium" v-model="personForm.phone" ></el-input>
                        </el-form-item>
                    </el-form>
                    <div class="formBtn">
                        <div class="m-edit-btn" @click="cancel">取消</div>
                        <div class="m-edit-btn safe" @click="submitForm('personForm',personForm.invoice_id)">保存</div>
                    </div>
                </div>
            </div>


        </div>
    </div>
</template>


<script>
    import mheadTop from '../../../components/mobile/headTop';
    import {addOrEditInvoice,delInvoice,editInvoiceShow} from '../../../service/api'
    export default {
        name: 'mInvoiceAdd',
        data(){
            return{
                invoice_id:'',
                invoiceType:'0',
                personForm:{
                    invoice_id:'',
                    title:'',
                    address:'',
                    phone:''
                },
                companyForm:{
                    invoice_id:'',
                    ename:'',
                    taxnumber:'',
                    address:'',
                    phone:'',
                    bank:'',
                    banknumber:''
                },
                formLabelWidth: '90px',
                invoiceType:'0',//0是企业，1是个人
                personRules:{
                    title: [
                        {required:true,type:"string",message:"请输入名称",trigger:"blur"}
                    ],
                    phone: [
                        { required: true,type:"string", message: '请输入手机号',trigger:"blur"}
                    ],
                    address: [
                        {required:true,type:"string",message:"请填写地址",trigger:"blur"}
                    ]
                },
                companyRules:{
                    ename: [
                        {required:true, message:"请输入公司名称",trigger:"blur"}
                    ],
                    taxnumber: [
                        { required: true, message: '请输入公司税号',trigger:"blur"}
                    ],
                    address: [
                        {required:true, message:"请填写开票地址",trigger:"blur"}
                    ],
                    phone: [
                        { required: true, message: '请输入电话号码',trigger:"blur"}
                    ],
                    bank:[
                        { required: true, message: '请输入开户银行',trigger:"blur"}
                    ],
                    banknumber:[
                        { required: true, message: '请输入银行账户',trigger:"blur"}
                    ]
                }
            }
        },
        components:{
            mheadTop
        },
        mounted(){
            this.initData();
        },
        computed:{
        },
        methods:{
            async initData(){
                this.invoice_id = this.$route.query.invoice_id;
                this.invoiceType = String(this.$route.query.type);
                console.log("this.invoice_id:"+this.invoice_id);
                console.log("this.invoiceType:"+this.invoiceType);
                if(this.invoice_id){//编辑发票
                    var res = await editInvoiceShow(
                        this.invoice_id
                    );
                    if(res.type == 0){//0是企业，1是个人
                        this.companyForm.invoice_id = res.invoice_id;
                        this.companyForm.ename = res.ename;
                        this.companyForm.taxnumber = res.taxnumber;
                        this.companyForm.address = res.address;
                        this.companyForm.phone = res.phone;
                        this.companyForm.bank = res.bank;
                        this.companyForm.banknumber = res.banknumber;
                    }else{
                        this.personForm.invoice_id = res.invoice_id;
                        this.personForm.title = res.title;
                        this.personForm.address = res.address;
                        this.personForm.phone = res.phone;
                    }
                }

            },
            submitForm(formName,id) {
                id = id || '';
                this.$refs[formName].validate(async (valid) => {
                    if (valid) {
                        if(this.invoiceType=='0'){//0是企业；1是个人
                            var res = await addOrEditInvoice(
                                id,//invoice_id
                                '',//title
                                this.invoiceType,//type
                                this.companyForm.taxnumber,//taxnumber
                                this.companyForm.address,//address
                                this.companyForm.banknumber,//banknumber
                                this.companyForm.phone,//phone
                                this.companyForm.bank,//bank
                                this.companyForm.ename//ename
                            )
                        }else{
                            var res = await addOrEditInvoice(
                                id,
                                this.personForm.title,
                                this.invoiceType,//type
                                '',//taxnumber
                                this.personForm.address,//address
                                '',//banknumber
                                this.personForm.phone,//phone
                                '',//bank
                                ''//ename
                            )
                        }
                        if(res){
                            this.$message({
                                message: res.msg,
                                type: res.status==12000?'success':'warning'
                            });
                            if(res.status == 12000){
                                var newRes = await invoice();
                                this.calTable(newRes);
                                this.dialogFormVisible = false;
                            }
                        }
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            cancel(){
                this.$router.go(-1);
            }

        },
        created(){

        },

    }

</script>

<style lang="scss">
    @import '../../../style/mixin';
    body{
        height: 100%;
    }
    .mBg{
        background-color:$bggray;
        display: block;
        width:100%;
        min-height: px2rem(1140);
        padding:px2rem(90) 0 px2rem(104) 0;
    }
    .m-edit-add{
        background-color: #fff;
        padding:px2rem(20) px2rem(20) px2rem(60) px2rem(20);
    }
    .m_sel{
        font-size: px2rem(32);
    }
    .formBtn{
        width:100%;
        height: px2rem(104);
        position: fixed;
        bottom:0;
        left:0;
        background-color:$colorwhite;
        border-top:1px solid #eee;
        z-index: 1000;
        display: flex;
        flex-wrap: nowrap;
    }
    .m-edit-btn{
        width:50%;
        height: px2rem(104);
        line-height: px2rem(104);
        text-align: center;
    }
    .m-edit-btn.safe{
        background-color: $default;
        color:#fff;
    }
    .m-f-h .el-form-item__label{
        padding-bottom: 0;
        line-height: 20px;
    }
    .m-f-h .el-form-item__error{
        display: block;
    }


</style>



